const { loginUser } = require('./auth');

// Enhanced logging function
const log = (...args) => {
  console.log('[Login Function]', ...args);
};

exports.handler = async (event, context) => {
  // Set CORS headers
  const headers = {
    'Access-Control-Allow-Origin': '*',
    'Access-Control-Allow-Headers': 'Content-Type, Authorization',
    'Access-Control-Allow-Methods': 'POST, OPTIONS',
    'Content-Type': 'application/json',
  };

  // Handle preflight OPTIONS request
  if (event.httpMethod === 'OPTIONS') {
    return {
      statusCode: 200,
      headers,
      body: '',
    };
  }

  // Only allow POST requests
  if (event.httpMethod !== 'POST') {
    log('Error: Method not allowed', event.httpMethod);
    return {
      statusCode: 405,
      headers,
      body: JSON.stringify({ error: 'Method Not Allowed' }),
    };
  }

  try {
    // Parse request body
    const { email, password } = JSON.parse(event.body);
    
    // Validate input
    if (!email || !password) {
      log('Validation error: Missing required fields');
      return {
        statusCode: 400,
        headers,
        body: JSON.stringify({ error: 'Email and password are required' }),
      };
    }

    // Authenticate the user
    const { user, token } = await loginUser(email, password);
    
    log('User logged in successfully:', user.email);
    
    // Return success response with user data and token
    return {
      statusCode: 200,
      headers: {
        ...headers,
        'Set-Cookie': `token=${token}; HttpOnly; Path=/; Max-Age=604800; SameSite=Lax`,
      },
      body: JSON.stringify({ 
        message: 'Login successful',
        user,
        token
      }),
    };
  } catch (error) {
    log('Error:', error.message);
    
    // Handle specific error cases
    let statusCode = 401;
    let errorMessage = 'Invalid email or password';
    
    if (error.message.includes('connection')) {
      statusCode = 500;
      errorMessage = 'Database connection error';
    }
    
    return {
      statusCode,
      headers,
      body: JSON.stringify({ error: errorMessage }),
    };
  }
};
