const { Pool } = require('pg');
const bcrypt = require('bcryptjs');
const jwt = require('jsonwebtoken');

// Initialize database connection pool
const pool = new Pool({
  user: process.env.DB_USER || 'routeuser',
  host: process.env.DB_HOST || 'localhost',
  database: process.env.DB_NAME || 'routedb',
  password: process.env.DB_PASSWORD || 'routepass',
  port: parseInt(process.env.DB_PORT || '5432'),
});

// JWT secret - in production, this should be a long, random string stored in environment variables
const JWT_SECRET = process.env.JWT_SECRET || 'your-secret-key';
const JWT_EXPIRES_IN = '7d'; // Token expires in 7 days

/**
 * Register a new user
 * @param {string} email - User's email
 * @param {string} password - Plain text password
 * @param {string} name - User's name
 * @returns {Promise<Object>} - User data without password
 */
async function registerUser(email, password, name) {
  try {
    // Check if user already exists
    const existingUser = await pool.query(
      'SELECT id FROM users WHERE email = $1',
      [email]
    );

    if (existingUser.rows.length > 0) {
      throw new Error('Email already in use');
    }

    // Hash password
    const salt = await bcrypt.genSalt(10);
    const hashedPassword = await bcrypt.hash(password, salt);

    // Create user
    const result = await pool.query(
      'INSERT INTO users (email, password_hash, name) VALUES ($1, $2, $3) RETURNING id, email, name, created_at',
      [email, hashedPassword, name]
    );

    return result.rows[0];
  } catch (error) {
    console.error('Error registering user:', error);
    throw error;
  }
}

/**
 * Authenticate a user
 * @param {string} email - User's email
 * @param {string} password - Plain text password
 * @returns {Promise<Object>} - User data and JWT token
 */
async function loginUser(email, password) {
  try {
    // Find user by email
    const result = await pool.query(
      'SELECT id, email, password_hash, name FROM users WHERE email = $1',
      [email]
    );

    if (result.rows.length === 0) {
      throw new Error('Invalid credentials');
    }

    const user = result.rows[0];

    // Verify password
    const isMatch = await bcrypt.compare(password, user.password_hash);
    if (!isMatch) {
      throw new Error('Invalid credentials');
    }

    // Create JWT token
    const token = jwt.sign(
      { userId: user.id, email: user.email },
      JWT_SECRET,
      { expiresIn: JWT_EXPIRES_IN }
    );

    // Return user data (without password) and token
    const { password_hash, ...userData } = user;
    return { user: userData, token };
  } catch (error) {
    console.error('Error logging in user:', error);
    throw error;
  }
}

/**
 * Get user by ID
 * @param {string} userId - User ID
 * @returns {Promise<Object>} - User data without password
 */
async function getUserById(userId) {
  try {
    const result = await pool.query(
      'SELECT id, email, name, created_at FROM users WHERE id = $1',
      [userId]
    );

    if (result.rows.length === 0) {
      throw new Error('User not found');
    }

    return result.rows[0];
  } catch (error) {
    console.error('Error getting user by ID:', error);
    throw error;
  }
}

module.exports = {
  registerUser,
  loginUser,
  getUserById,
  JWT_SECRET,
};
